$moblie-width: 'only screen and (max-width: 599px)';
@mixin response($breakpoint) {
    @if ($breakpoint == mobile) {
        @media #{$moblie-width} {
            @content;
        }
    }
}
.wrap{
    position: relative;
    display: flex;
    flex-flow: wrap;
    width: 100%;
    height: 100%;  
    top:-1em;
}

.container{
    position: relative;
    width: 14.7em ;
    height: 18.3em;
    margin: 2em 2em;
    border: 0px solid transparent;
    border-radius: 0.35em;
    padding: 0;
    overflow: hidden;
    background-color: var(--light-element);
    box-shadow: 0 0 10px var(--light-shadow);
    .box{
        position: relative;
        padding: 0 1.5em;
        margin: 0;
        left: 0;
        .image{
            position: relative;
            width: 220.5px;
            height: 135px;
            left: calc(-1.5em - 1px);
            box-shadow: 0 0 10px var(--light-shadow);
            border-radius: 4px;
            padding: 0;          
        }
        .name{
            font-size: 18px ;
            font-weight: 700;
            margin-bottom: 0.5em;
            margin-top: 0.5em;
            color: var(--light-color);
        }
        .details{
            margin: 0.3em 0;
            font-weight: 600;
            color: var(--light-color);
            .info{
                font-weight: 500;
            }
        }
    }
}
.pagination-parent{
    position: relative;
    width: 100%;
    right: 15px;
}
.loader{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.loading{
    font-size: 22px;
    font-weight: 700;
    color:#0000;
    background:linear-gradient(90deg,red calc(50% + 0.5ch),#000 0) right/calc(200% + 1ch) 100%;
    background-clip:text;
    animation: loader 1.5s infinite steps(10);
}
@keyframes loader {
    to{background-position: left}
}
.spinner{
    width: 40px;
    height: 40px;
    padding: 8px;
    aspect-ratio: 1;
    border-radius: 50%;
    background: #25b09b;
    --_m: 
      conic-gradient(#0000 10%,#000),
      linear-gradient(#000 0 0) content-box;
    -webkit-mask: var(--_m);
            mask: var(--_m);
    -webkit-mask-composite: source-out;
            mask-composite: subtract;
    animation: s3 1s infinite linear;
  }
  @keyframes s3 {to{transform: rotate(1turn)}}

@include response(mobile){
    .wrap{
        display: flex;
        justify-content: space-between;
        flex-flow: wrap;
        width: 100%;
        height: 100%;  
    }
    .container{
        position: relative;
        top: 1em;
        width: 15em ;
        height: 19em;
        margin: 1em 1em;
        border: 1px solid transparent;
        border-radius: 0.35em;
        box-shadow: 0 0 0.5em gainsboro;
    }
    .pagination{
        position: relative;
        width: 100%;
    }
}