.filter{
    width: 18%;
    max-width: 180px;
    height: 3em;
    border: 1px solid transparent;
    border-radius: 0.35em;
    background-color: var(--light-element);
    color: var(--light-color);
    box-shadow: 0 0 0.5rem var(--light-shadow);
    left: 0;
}
.title{
    position: relative;
    top: 0.8em;
    left: 0.8em;
}
.arrow-down{
    position: relative;
    display: inline;
    width: 16px;
    height: 16px;
    border-top: 8px solid;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-bottom: 8px solid transparent;
    float: right;
    top: -0.18em;
    margin-right: 1em;
}
.box-tippy{
    position: relative;
    width: 18vw;
    max-width: 180px;
    height: fit-content;
    top: 1.3em;
    border: 1px solid transparent;
    border-radius: 0.2em;
    background-color: var(--light-element);
    box-shadow: 0 0 10px var(--light-shadow);
    color: var(--light-color);
    padding: 1em 0;
}
.checkbox{
    position: relative;
    display: flex;
    width: 100%;
    height: 2em;
    padding: 0px 0%;
    font-size: 18px;
    font-weight: 600;
    padding: 6px 0 0 28px;
    
    &:hover{
        background-color: var(--light-hover);
        border-radius: 4px;
        transition: all 0.3s;
    }
}
.input-filter{
    width: 16px;
    height: 16px;
    margin-right: 1em;
    margin-top: 3px;
}
