.search{
    position: relative;
    width: 33vw;
    min-width: 280px;
    max-width: 320px;
    height: 3em;
    border: 0px solid transparent;
    border-radius: 0.35em;
    background-color: var(--light-element);
    box-shadow: 0 0 0.5rem var(--light-shadow);
    color: var(--light-color);
}
.inside-tippy{
    width: 100%;
    height: 100%;
}
.input-search {
    position: relative;
    width: 90%;
    height: 90%;
    left: 5%;
    top: 5%;
    border: none;
    outline: none;
    background-color: transparent;
    caret-color: red;
    box-shadow: none !important;
    color: var(--light-color);
}
input::placeholder{
    color: var(--light-color);
}
.faXmark{
    position: relative;
    left: -2.3em;
    top: 0.2em;
}
.vertical-line{
    position: absolute;
    width: 0;
    height: 70%;
    border-left: 1px solid rgb(112, 110, 110);
    float: right;
    right: 2.5em;
    top: 0.4em;
}
.faSearch{
    position: absolute;
    float: right;
    right: 1.2em;
    top: 0.9em;
    scale: 1.3;
}
.box-tippy{
    position: relative;
    width: 33vw;
    min-width: 280px;
    max-width: 320px;
    height: 20em;
    max-height: 500px;
    border: 1px solid transparent;
    border-radius: 5px;
    box-shadow: 0 0 10px var(--light-shadow);
    background-color: var(--light-element);
    color:var(--light-color);
    .title{
        position: relative;
        top: 0.5em;
        text-align: center;
    }
    .horizontal-line{
        position: relative;
        height: 0;
        width: 86%;
        border-top: 1px solid rgb(160, 160, 160) ;
        left: 7%;
        top: 0.5em;
    }
    .box-result{
        position: relative;
        width: 100%;
        height: 80%;
        max-height: 450px;
        overflow-y: overlay;
        top: 1.5em;
        .display-result{
            position: relative;
            display: block;
            width: 86%;
            height: 2.5em;
            left: 7%;
            margin-bottom: 0.5em;
            margin-top: 0.2em;
            &:hover{
                background-color: var(--light-hover);
            }
            img{
                position: relative;
                width: 2em;
                height: 2em;
                border-radius: 3px;
                box-shadow: 0 0 8px rgb(179, 178, 178);
                top: 3px;
            }
            span{

                position: relative;
                display: inline-block;
                width: 80%;
                left: 20px;
                top: 0px;
                font-size: 18px;
                font-weight: 600;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                
            }
        }
        p{
            position: relative;
            top: 1.2em;
            left: 7%;
            width: 80%;
        }  
    }
    
}